"use strict";

module.exports = function () {

    return {

        /**
         *    
         *
         * @param {String} title 
         * @param {String} message 
         * @returns {*}
         */

        error: function error(title, message) {
            return this.message(title, message, "error");
        },


        /**
         *  Success 
         * @param {String} title 
         * @param {String} message 
         * @returns {*}
         */
        success: function success(title, message) {
            return this.message(title, message, "success");
        },


        /**
         *  
         *
         * @param {String} title 
         * @param {String} message 
         * @param {String} type   (error, success)
         * @returns {*}
         */
        message: function message(title, _message, type) {
            return swal(title, _message, type || 'success');
        },


        /**
         *    
         *
         * @param {String} title 
         * @param {String} message 
         * @param {Function} callback    
         */
        confirm: function confirm(title, message) {
            return swal({
                title: title,
                text: message || '',
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3c8dbc',
                cancelButtonColor: '#d33',
                confirmButtonText: trans('lang.button.yes')
            });
        },


        /**
         *     
         *
         * @param {String} title
         * @param {String} message
         * @param {Function} callback    
         * @param {String} inputPlaceholder     
         */
        prompt: function prompt(title, message, inputPlaceholder) {
            return swal({
                title: title,
                text: message || '',
                input: 'text',
                showCancelButton: true,
                closeOnConfirm: false,
                inputPlaceholder: inputPlaceholder || ''
            });
        }
    };
}();


